import React from 'react';
import {StyleSheet, View} from 'react-native';
import {Text, useTheme} from 'react-native-paper';
import {RootStackParamList, ROUTES} from '../routes';
import InputForm from '../components/InputForm';
import {StackNavigationProp} from '@react-navigation/stack';

type TWelcomeScreenNavigationProp = StackNavigationProp<
  RootStackParamList,
  'Welcome'
>;

interface IWelcomeScreenProps {
  navigation: TWelcomeScreenNavigationProp;
}
const WelcomeScreen = ({navigation}: IWelcomeScreenProps) => {
  const theme = useTheme();

  const onSuccess = () => {
    navigation.navigate(ROUTES.GITHUB_ISSUES);
  };
  return (
    <View style={styles.container}>
      <Text style={[styles.title, theme.variants.title]}>
        Welcome to Gibhub issues
      </Text>
      <InputForm onSuccess={onSuccess} />
    </View>
  );
};
export default WelcomeScreen;
const styles = StyleSheet.create({
  container: {
    margin: 10,
  },
  title: {
    marginVertical: 20,
  },
});
